import React from 'react'
import '../styles/menu.css'

function Menu(props: any) {
  return (
    <div className='navigation' id='navigation'>
      <ul className='menu'>
        <li><h3 onClick={props.mainOnClick} className='anchor'>главная</h3></li>
        <li><h3 onClick={props.aboutOnClick} className='anchor'>о компании</h3></li>
        <li><h3 onClick={props.catalogOnClick} className='anchor'>каталог</h3></li>
        <li><h3 onClick={props.servicesOnClick} className='anchor'>услуги</h3></li>
        <li><h3 onClick={props.contactsOnClick} className='anchor'>контакты</h3></li>
      </ul>
    </div>
  )
}

export default Menu
